import {combineReducers, applyMiddleware} from 'redux'
import {createStore} from 'redux'
import thunk from 'redux-thunk'
import {composeWithDevTools} from 'redux-devtools-extension'
import {productListReducer, productDetailsReducer, adminProductListReducer, adminUpdateProductReducer, adminCreateProductReducer, adminDeleteProductReducer} from './reducers/productReducers'
import { cartReducer } from './reducers/cartReducers'
import { userLoginReducer, userRegisterReducer, userDetailReducer, userUpdateProfileReducer, usersListReducer, deleteUserReducer, updateUserReducer, getUserByIdReducer} from './reducers/userReducers'
import { orderCreateReducer, orderDetailsReducer, updateToPaidReducer, getUserOrdersReducer, adminGetOrdersReducer, updatetoDeliveredReducer, updateToDeliveredReducer } from './reducers/ordersReducers'


const reducer = combineReducers({
    productList: productListReducer,
    productDetails: productDetailsReducer,
    cart: cartReducer,
    userLogin: userLoginReducer,
    userRegister: userRegisterReducer,
    userDetail: userDetailReducer,
    userUpdateProfile: userUpdateProfileReducer,
    orderCreate: orderCreateReducer,
    orderDetails: orderDetailsReducer,
    orderPay:updateToPaidReducer,
    userOrders: getUserOrdersReducer,
    userList: usersListReducer,
    userDelete:deleteUserReducer,
    getUser:getUserByIdReducer,
    updateUser: updateUserReducer,
    adminProductsList: adminProductListReducer,
    updateProduct: adminUpdateProductReducer,
    createProduct: adminCreateProductReducer,
    deleteProduct: adminDeleteProductReducer,
    orderList:adminGetOrdersReducer,
    orderDeliver: updateToDeliveredReducer,
})

const cartItemsFromStorage = localStorage.getItem('cartItems') ? JSON.parse(localStorage.getItem('cartItems')) : []
const userInfoFromStorage = localStorage.getItem('userInfo') ? JSON.parse(localStorage.getItem('userInfo')) : null
const shippingAddressFromStorage = localStorage.getItem('shippingAddress') ? JSON.parse(localStorage.getItem('shippingAddress')) : {}
const paymentMethodFromStorage = localStorage.getItem('paymentMethod') ? JSON.parse(localStorage.getItem('paymentMethod')) : null


const initialState = {
    cart: {cartItems: cartItemsFromStorage, shippingAddress: shippingAddressFromStorage, paymentMethod: paymentMethodFromStorage},
    userLogin: {userInfo: userInfoFromStorage},
    
}

const middleWare = [thunk]

const store = createStore (reducer, initialState, composeWithDevTools(applyMiddleware(...middleWare)))

export default store

