import { ORDER_CREATE_REQUEST, ORDER_CREATE_SUCCESS, ORDER_CREATE_FAIL, 
    ORDER_DETAILS_REQUEST, ORDER_DETAILS_SUCCESS, ORDER_DETAILS_FAIL, 
    ORDER_PAY_REQUEST, ORDER_PAY_SUCCESS, ORDER_PAY_RESET, ORDER_PAY_FAIL, ORDERS_GET_REQUEST, ORDERS_GET_SUCCESS, ORDERS_GET_FAIL, ADMIN_ORDERS_GET_REQUEST, ADMIN_ORDERS_GET_SUCCESS, ADMIN_ORDERS_GET_FAIL, ORDER_DELIVER_REQUEST, ORDER_DELIVER_SUCCESS, ORDER_DELIVER_FAIL } from "../constants/orderConstants";
import axios from 'axios'
import {logout} from './userActions'
export const createOrder = (order) => async(dispatch, getState) => {
    try {
        dispatch({
            type: ORDER_CREATE_REQUEST

        })

        const { userLogin: { userInfo } } = getState();

        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        const { data } = await axios.post('/api/orders', order, config)

        dispatch({
            type: ORDER_CREATE_SUCCESS,
            payload: data
        })


    } catch (error) {
        dispatch({
            type: ORDER_CREATE_FAIL,
            payload:error.response && 
            error.response.data.message ? 
            error.response.data.message :
            error.message,
        })
        
    }
}




